
<template>
  <div class="support-container">
    <div class="user"><p>{{obj}}</p></div>
    <h3>为TA冲榜</h3>
    <div class="star"></div>
    <div class="pay-container">
      <div class="pay-item" v-for="(item,i) in payItems"
      :key="i">
        <div>{{item.money}}元(+{{item.hotValue}}热度)</div>
        <input type="checkbox" :checked="item.id == currentId" name="checkboxinput" @click="checkOne(item.id)" >
      </div>
    </div>
    <button class="btn subpay" @click="Pay">用爱发电</button>
  </div>
</template>

<script>
import utils from '../utils'
export default {
  data () {
    return {
      obj: '',
      currentId: 2,
      payItems: [{
        id: 0,
        money: 2,
        hotValue: 30
      }, {
        id: 1,
        money: 5,
        hotValue: 100
      }, {
        id: 2,
        money: 10,
        hotValue: 300
      }, {
        id: 3,
        money: 20,
        hotValue: 700
      }, {
        id: 4,
        money: 50,
        hotValue: 2000
      }, {
        id: 5,
        money: 100,
        hotValue: 5000
      }]
    }
  },
  methods: {
    checkOne (id) {
      this.currentId = id
    },
    Pay () {
      console.log(this.payItems.find(v => v.id === this.currentId).money)
    }
  },
  async mounted () {
    // https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxfbb29b02f3f076b8&redirect_uri=http%3A%2F%2Fwww.pkfis.cn%2Fwx%2Fpublic%2FSupport&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect
    const {code} = JSON.parse(localStorage.getItem('query'))
    console.log(code)
    const loginRes = await utils.get('/login', {'code': code})
    console.log(loginRes)
    this.obj = JSON.stringify(loginRes)
    // this.openId = this.$route.query && (this.$route.query.openId || 'undefined')
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.support-container{
  margin:0 10px;
  .user{
    height: 60px;
  }
  .star{
    height: 60px;
  }
  .pay-container{
    width:100%;
    display: flex;
    flex-direction: column;
    .pay-item{
      display: flex;
      flex-direction: row;
      font-size: 14px;
      input{
        margin-left: auto;
        margin-right: 5px;
      }
    }
  }
  .subpay{
    margin-top:20px;
  }
}

</style>



